/* Prototype */
.ui.table {
  width: 100%;
  background: var(--smui-table-background, #FFFFFF);
  margin: 1em 0em;
  border: 1px solid var(--smui-table-border-color, rgba(34, 36, 38, 0.15));
  box-shadow: none;
  border-radius: 0.28571429rem;
  text-align: left;
  color: var(--smui-table-color, rgba(0, 0, 0, 0.87));
  border-collapse: separate;
  border-spacing: 0px;
}
.ui.table:first-child {
  margin-top: 0em;
}
.ui.table:last-child {
  margin-bottom: 0em;
}
/*******************************
             Parts
*******************************/
/* Table Content */
.ui.table th,
.ui.table td {
  transition: background 0.1s ease, color 0.1s ease;
}
/* Headers */
.ui.table thead {
  box-shadow: none;
}
.ui.table thead th {
  cursor: auto;
  background: var(--smui-table-header-background, #F9FAFB);
  text-align: inherit;
  color: var(--smui-table-header-color, rgba(0, 0, 0, 0.87));
  padding: 0.92857143em 0.78571429em;
  vertical-align: inherit;
  font-style: none;
  font-weight: bold;
  text-transform: none;
  border-bottom: 1px solid rgba(34, 36, 38, 0.1);
  border-left: none;
}
.ui.table thead tr > th:first-child {
  border-left: none;
}
.ui.table thead tr:first-child > th:first-child {
  border-radius: 0.28571429rem 0em 0em 0em;
}
.ui.table thead tr:first-child > th:last-child {
  border-radius: 0em 0.28571429rem 0em 0em;
}
.ui.table thead tr:first-child > th:only-child {
  border-radius: 0.28571429rem 0.28571429rem 0em 0em;
}
/* Footer */
.ui.table tfoot {
  box-shadow: none;
}
.ui.table tfoot th {
