.ui.uncover.sidebar {
  transform: translate3d(0, 0, 0);
  z-index: 1;
}
/* End */
.ui.visible.uncover.sidebar {
  transform: translate3d(0, 0, 0);
  transition: transform 500ms ease;
}
/*--------------
